html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lexend", sans-serif;
  font-size: 1.6rem;
  line-height: 1.15;
}

span {
  text-transform: capitalize;
}

p {
  color: #000;
  line-height: 2;
}

a {
  text-decoration: none;
  background-color: transparent;
}

img {
  display: block;
  max-width: 100%;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

ul {
  padding: 0;
}

li {
  list-style: none;
}

[class*=contenedor] {
  max-width: 1200px;
  width: 95%;
  margin: 0 auto;
}

.text-center {
  text-align: center;
}

.avif .header {
  background-image: url("../img/header.avif");
}

.webp.notavif .header {
  background-image: url("../img/header.webp");
}

.notwebp.notavif .header {
  background-image: url("../img/header.jpg");
}

.header {
  padding: 2.5rem 0;
  background-position: center top;
}
.header__menu {
  display: none;
}
@media (min-width: 768px) {
  .header__menu {
    display: flex;
    justify-content: space-between;
  }
}
.header__menu p {
  color: #fff;
  margin: 0;
  font-weight: 300;
}
.header__anfitrion {
  text-transform: capitalize;
}

.menu ul {
  display: flex;
  flex-direction: row;
  gap: 3rem;
  margin: 0;
}
.menu ul li {
  margin: 0;
}

.hero .form__content {
  background-color: #fff;
  border: none;
  border-radius: 5rem;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .hero .form__content {
    display: flex;
    padding: 0;
    align-items: center;
    justify-content: space-between;
    max-width: 90rem;
    margin: 3rem auto 0;
  }
}
.hero .form__label {
  display: block;
  font-weight: 900;
}
.hero .form input {
  display: block;
  width: 100%;
  border: none;
}
.hero .form__campo {
  padding: 1rem 2rem;
}
.hero .form__campo:hover {
  border-radius: 5rem;
  background-color: #f0f0f0;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.4);
}
@media (min-width: 768px) {
  .hero .form__campo {
    padding: 1rem 2rem;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
.hero .form__label {
  display: block;
  font-size: 1.2rem;
}
@media (min-width: 768px) {
  .hero .form__label {
    font-size: 1.6rem;
  }
}
.hero .form__btn {
  padding: 1.5rem 2rem;
  border-radius: 2rem;
  text-transform: uppercase;
  background-image: radial-gradient(circle at center, #ff385c, #bd1e59);
  color: #fff;
}